import React from 'react';
import classes from './Post.module.css'

const Post = (props) => {
  return (

    <div className={classes.item}>
      <img src='data:image/png;base64,' />
      {props.message}
      <div>
        <span>Like</span> {props.likesCount}
      </div>
    </div>
  )
}

export default Post;